.button{
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    height:32px;
    background:#fff;
    border:1px solid rgba(153,153,153,1);
    border-radius:4px;
    padding:1px 1em;
    position: relative;
    overflow: hidden;
    font-size: 12px;
}
.button:focus{
    outline:none;
    border-color:black;
}
.button .value{
    position: relative;
    z-index: 1;
}
.button .circle{
    display:block;
    height:10px;
    width:10px;
    border-radius:50%;
    background: #dedede;
    opacity:0.5;
    -webkit-animation: circle 0.5s forwards;
            animation: circle 0.5s forwards;
    position:absolute;
    z-index: 0;
}
@-webkit-keyframes circle {
    0%{
        -webkit-transform: scale(0);
                transform: scale(0)
    }
    100%{
        -webkit-transform: scale(10);
                transform: scale(10)
    }
}
@keyframes circle {
    0%{
        -webkit-transform: scale(0);
                transform: scale(0)
    }
    100%{
        -webkit-transform: scale(10);
                transform: scale(10)
    }
}



