.inputWrapper {
    width:100%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 16px ;
}
.inputWrapper input{
    width:60%;
    padding:0 6px;
    border:none;
    border-bottom: 1px solid #ddd;
    background-color: inherit;
    color: white;
    line-height: 32px;
    height:32px;
    font-size: 14px;
}
.inputWrapper input:focus{
    outline:none;
}
.inputWrapper button{
margin-left:20px;
}



